html {
  scroll-behavior: smooth;
}
body {
  background-color: #000;
  text-align: center;
  color: #fff;
  font-family: 'Roboto Condensed', sans-serif;
  text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;
}
header {
  z-index: 10;
}
footer {
  margin: 0 0 2rem 0;
}
nav {
  margin: auto;
  width: 400px;
  top: 1rem;
  z-index: 15;
}
nav a {
  margin: 0 2em;
  text-decoration: none;
  color: #ddd;
  transition: .3s;
}
nav a:hover {
  color: #195cd1;
  text-decoration: none;
}

.glyph-menu {
  opacity: 0.5;
  cursor: pointer;
  position: fixed;
  top: 45%;
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
  max-width: 30rem;
}
.glyphicon-menu-left {
  left: 0;
}
.glyphicon-menu-right {
  right: 0;
}
.pic {
  opacity: 1;
  transition: 0.5s;
}
.pic:hover {
  opacity: 0.8;
  box-shadow: -2px -2px 10px #000, 2px -2px 10px #000, -2px 2px 10px #000, 2px 2px 10px #000;
}
.split {
  min-height: 3rem;
}
.big-split {
  min-height: 4rem;
}
.glyphicon {
  font-size: 5rem;
}
.to-top, .sound-icon {
  opacity: 0.6;
  margin: 2rem;
  position: fixed;
  cursor: pointer;
  bottom: 0;
}
.to-top {
  right: 0;
}
.sound-text {
  margin: 0;
}
#yatklvb {
  color: #fd4f0a;
  font-size: 4rem;;
  margin: .5rem;
}
.blank {
  min-height: 100vh;
}
.bg-video {
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  min-width: 100vw;
  z-index: -1;
}
.text-bg {
  background-color: rgba(0, 0, 0, 0.75);
  margin: auto;
  padding: 1rem;
}
.text-bg h2 {
  margin: 0;
}
.about {
  max-width: 800px;
}
.bios {
  max-width: 700px;
  margin-top: 1rem;
}
.bio-pic {
  max-width: 25rem;
}
.shows {
  max-width: 600px;
  cursor: pointer;
  position: relative;
}
.social-logo {
  width: 3.5rem;
  cursor: pointer;
  transition: .3s;
  fill: #ddd;
  opacity: 0.75;
  margin: 0 .5rem;
}
.social-logo:hover {
  opacity: 1;
}
.spotify:hover {
  fill: #1ED760;
}
.instagram {
  border-radius: 35%;
}
.instagram:hover {
  fill: radial-gradient(circle at 30% 107%, #fdf497 0%, #fdf497 5%, #fd5949 45%,#d6249f 60%,#285AEB 90%);
  background: radial-gradient(circle at 30% 107%, #fdf497 0%, #fdf497 5%, #fd5949 45%,#d6249f 60%,#285AEB 90%);
}
.soundcloud:hover {
  fill: #FF3300;
}
.youtube:hover {
  fill: #ff0000;
}
.show-details {
  display: inline-block;
  top: 50%;
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
  max-width: 30rem;
}
.show-details h2,
.show-details h3,
.show-details p {
  margin: .25rem;
}
.full-image {
  max-width: 90vw;
  max-height: 80vh;
}

/* Modals */
.modal-bg {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
  align-items: center;
  justify-content: center;
  z-index: 100;
}
.modal-box {
  background-color: rgba(0, 0, 0, 0.5);
  display: inline-block;
  margin-top: 5vh;
}
.modal-content {
  margin: 1.5em;
}
.modal-box h3,
.modal-box h4 {
  margin: .2em;
}
.modal-top-row {
  display: relative;
  text-align: right;
  padding: .33em 2.4em;
}
.x-close-button {
  border-radius: 100%;
  padding: .3em .60em .15em .65em;
  border: 1px solid #ddd;
  display: inline-block;
  position: absolute;
  cursor: pointer;
  color: #ddd;
  font-weight: 800;
  transition: all 0.2s;
}
.x-close-button:hover {
  background-color: #666;
}

.one {
  color: #fd4f0a;
}
.two {
  color: #db051d;
}
.three {
  color: #6b060e;
}
.four {
  color: #264478;
}
.five {
  color: #161330;
}

@media screen and (max-width: 820px) {
  .glyphicon {
    font-size: 3.5rem;
  }
  .social-logo {
    width: 2.5rem;
  }
}
@media screen and (max-width: 519px) {
  .show-details {
    transform: none;
    max-width: 30rem;
  }
  nav {
    width: 300px;
  }
  nav a {
    margin: 0 1em;
  }
}
